import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './CompanySearch.css';
import PropTypes from 'prop-types';
import logoUrl from './untitled.png';

class CompanySearch extends React.Component {

  constructor(props) {
    super(props);
    this.state = {type:''};
  }

  getChildContext() {
    return {type: this.state.type};
  }

  static childContextTypes = {
    type: PropTypes.string
  }

  static contextTypes = {
    searchString: PropTypes.string
  }

  updateSearch = (input) => {
    console.log(input.target.value)
    const data = input.target.value
    console.log('expected: yeet, actual: ', this.context.searchString)
    this.context.searchString = data
    this.setState({data})
      console.log('updated the search string')
  }

  render() {
    const filterText = this.props.filterText;
    const minorityLed = this.props.minorityLed;
    const minorityFounded = this.props.minorityFounded;
    const logo = "https://firebasestorage.googleapis.com/v0/b/spiffy-d8ddc.appspot.com/o/untitled.png?alt=media&token=09bc9d32-d4e7-42e4-8992-cbddbe0ce0d6"
    console.log(this.context)
    return (
      <div className={s.root}>
        <form>
          <img src={logo} width="38" height="38" alt="Reac" />
          <input
            onChange={this.updateSearch.bind(this)}
            className={s.search}
            type="text"
            placeholder="Search..."
            value={this.filterText}
          />
          <p>
            <button className={s.filterButton}>Minority Led</button>
            <button className={s.filterButton}>Minority Founded</button>
            <button className={s.filterButton}>Size</button>
            <button className={s.filterButton}>Revenue</button>
          </p>
        </form>
      </div>
    );
  }
}

export default withStyles(s)(CompanySearch);
